<script setup lang="ts">
import { ref, onMounted } from 'vue'
import { useNotificationStore } from '@/stores/notifications'
import DashboardHeader from '@/components/layout/DashboardHeader.vue'
import { ChartBarIcon, XMarkIcon, CheckIcon } from '@heroicons/vue/24/outline'

const notifications = useNotificationStore()

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const selectedSessionId = ref('')
const evaluationData = ref(null)
const loading = ref(false)
const error = ref('')
const activeTab = ref('context')
const showFeaturePreview = ref(false)

// Ê†áÁ≠æÈ°µÂÆö‰πâ
const tabs = [
  { id: 'context', name: 'ËØÑ‰º∞‰∏ä‰∏ãÊñá' },
  { id: 'results', name: 'ËØÑ‰º∞ÁªìÊûú' },
  { id: 'associations', name: 'ÂÖ≥ËÅîÂàõÂª∫' }
]

// APIÂü∫Á°ÄURL
const API_BASE = 'http://localhost:5000/api'

// Â∑•ÂÖ∑ÂáΩÊï∞
const formatDuration = (duration: number | null | undefined): string => {
  if (!duration && duration !== 0) return 'Êú™Áü•'
  if (duration < 0.001) return '<1ms'
  if (duration < 1) return `${(duration * 1000).toFixed(1)}ms`
  return `${duration.toFixed(3)}s`
}

// Ëé∑ÂèñÊÉÖÊÑüemoji
const getEmotionEmoji = (emotion: string): string => {
  const emotionMap: { [key: string]: string } = {
    'joy': 'üòä',
    'sadness': 'üò¢',
    'anger': 'üò†',
    'fear': 'üò®',
    'surprise': 'üòÆ',
    'disgust': 'ü§¢',
    'positive': 'üòä',
    'negative': 'üòî',
    'neutral': 'üòê',
    'happy': 'üòÑ',
    'excited': 'ü§©',
    'calm': 'üòå',
    'confused': 'üòï',
    'frustrated': 'üò§'
  }
  return emotionMap[emotion.toLowerCase()] || 'üòê'
}

// Âä†ËΩΩËØÑ‰º∞Êï∞ÊçÆ
const loadEvaluationData = async () => {
  if (!selectedSessionId.value.trim()) {
    error.value = 'ËØ∑ËæìÂÖ•‰ºöËØùID'
    return
  }

  loading.value = true
  error.value = ''
  
  try {
    const response = await fetch(`${API_BASE}/session/${selectedSessionId.value}/evaluation`)
    
    if (!response.ok) {
      const errorData = await response.json()
      throw new Error(errorData.error || `HTTP ${response.status}`)
    }
    
    const data = await response.json()
    evaluationData.value = data
    
    notifications.add({
      type: 'success',
      title: 'ËØÑ‰º∞Êï∞ÊçÆÂä†ËΩΩÊàêÂäü',
      message: `Â∑≤Âä†ËΩΩ‰ºöËØù ${selectedSessionId.value} ÁöÑËØÑ‰º∞Êï∞ÊçÆ`
    })
    
  } catch (err: any) {
    error.value = err.message || 'Âä†ËΩΩÂ§±Ë¥•'
    evaluationData.value = null
    
    notifications.add({
      type: 'error',
      title: 'Âä†ËΩΩÂ§±Ë¥•',
      message: error.value
    })
  } finally {
    loading.value = false
  }
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÁöÑÂàùÂßãÂåñ
onMounted(() => {
  console.log('EvaluationViewer mounted')
})
</script>